<template>
  <div id="op-header" class="op-header" :style="{height}">
    <div class="op-header_inner">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'op-header',
    props: {
      height: {
        type: String,
        default: '60px'
      }
    }
  }
</script>

<style lang="less">
  .op-header {
    padding: 0 10px;

    .op-header_inner {
      height: 100%;
      border-bottom: 1px solid #f2f2f2;
    }
  }

</style>



