<template>
  <div class="op-nav">
     <el-breadcrumb separator=">" class="m-header-navbar-list">
            <el-breadcrumb-item :to="{ path: '/' }" class="ml-10">
              <i class="icon iconfont"></i>
            </el-breadcrumb-item>
            <el-breadcrumb-item v-for="(val,index) in $route.meta.nav" :key="index">{{val}}</el-breadcrumb-item>
          </el-breadcrumb>
  </div>
</template>

<script>
  export default {
    name: 'op-nav'
  }
</script>

<style lang="less">
  .op-nav {
    float: left;
    width: 40%;
    padding:23px 0;
  }
</style>



