<template>
  <op-container>
    <op-sidebar :width="sidebarWidth"></op-sidebar>
    <op-body :headHeight="headHeight" :marginLeft="sidebarWidth">
      <router-view> </router-view>
    </op-body>
  </op-container>
</template>
<script>
  import opContainer from 'components/base/container.vue'
  import opSidebar from 'components/base/sidebar/index.vue'
  import opBody from 'components/base/opBody.vue'
  export default {
    name: 'layout',
    data () {
      return {
        headHeight: '60px',
        sidebarWidth: '200px'
      }
    },
    components: {opContainer, opBody, opSidebar}
  }
</script>




