import Vue from 'vue'
import 'babel-polyfill'
import App from './App'
import store from '@/store'
import router from '@/router/home'
// import ElementUI from 'element-ui'
import permission from '@/permission'

// Vue.use(ElementUI)
Vue.config.productionTip = false
/* eslint-disable no-new */

// 路由权限过滤
permission(router)
new Vue({
  el: '#app',
  store,
  router,
  template: '<App/>',
  components: { App }
})



