<template>
  <div id="op-sidebar" class="op-sidebar" :style="{width}">
  <scroll-bar> 
    <div class="sidebar-inner">
      <avatar></avatar>
        <el-menu mode="vertical" :default-active="$route.path" background-color="#F2F2F2" text-color="#333" active-text-color="#ffffff">
          <sidebar-item :routes="permissionList"></sidebar-item>
        </el-menu>
      <!-- <op-menu :menus="permissionList"></op-menu> -->
    </div>
  </scroll-bar>
    
  </div>
  
</template>

<script>
  import { mapGetters } from 'vuex'
  import avatar from '../avatar.vue'
  import SidebarItem from './sidebarItem.vue'
  import ScrollBar from 'components/base/scrollBar.vue'
  export default {
    name: 'op-sidebar',
    props: {
      width: {
        type: String,
        default: '200px'
      }
    },
    computed: {
      ...mapGetters(['permissionList'])
    },
    components: {avatar, SidebarItem, ScrollBar},
    mounted: function () {
      // {id:'id/编号',name:'名称',target:'目标地址'}
      console.log(this.permissionList)
    }
  }
</script>

<style lang="less">
  .op-sidebar {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    height: 100%;
    border-right: 1px solid #DFDFDF;
    overflow: hidden;
    .sideber-inner{
      width:100%;
      height: 100%;
      position: relative;
    }
  }
</style>



