<template>
  <div class="menu-wrapper">
    <template v-for="item in routes">
      <a v-if="item.subList.length===1 && item.isNavigation === 1" :href="item.meanUrl" :key="item.subList[0].id">
        <el-menu-item :index="item.subList[0].meanUrl" :class="{'submenu-title-noDropdown':!isNest}" >
          <!-- <svg-icon v-if="item.subList[0].meta&&item.subList[0].icon" :icon-class="item.subList[0].icon"></svg-icon> -->
          <span v-if="item.subList[0]">{{item.permissionName}}</span>
        </el-menu-item>
      </a>
      <el-submenu v-else :index="item.permissionName||item.meanUrl" :key="item.id">
        <template slot="title">
          <!-- <i v-if="item.meta&&item.icon" :icon-class="item.icon"></i> -->
          <span v-if="item.permissionName">{{item.permissionName}}</span>
        </template>

        <template v-for="child in item.subList" v-if="item.subList.length > 0">
          <sidebar-item :is-nest="true" class="nest-menu" v-if="child.subList&&child.subList.length>0 && child.isNavigation ===0 " :routes="[child]" :key="child.meanUrl"></sidebar-item>

          <a v-else :href="(item.meanUrl+child.meanUrl) === '//'? '/': (item.meanUrl+child.meanUrl)" :key="child.id" @click = "reloadUrl">
            <el-menu-item  :index="child.meanUrl"> 
              <!-- <i v-if="child.meta&&child.icon" :icon-class="child.icon"></i> -->
              <span v-if="child.permissionName && child.isNavigation === 1">{{child.permissionName}}</span>
            </el-menu-item>
          </a>
        </template>
      </el-submenu>

    </template>
  </div>
</template>

<script>
// import { generateTitle } from '@/utils/i18n'
export default {
  name: 'SidebarItem',
  props: {
    routes: {
      type: Array
    },
    isNest: {
      type: Boolean,
      default: false
    }
  },
  mounted () {
    console.log(this.routes)
  },
  methods: {
    // generateTitle
    reloadUrl () {
      location.reload()
    }
  }
}
</script>
<style>
 .el-submenu__title{
   padding-left: 20px !important;
 }
 .el-submenu.is-opened .el-menu .el-menu-item{
   background-color: #ffffff !important;
 }
.el-submenu.is-opened .el-menu .el-menu-item.is-active{
   background-color: #64C3DF !important;
 }
</style>




