<template>
  <div class="op-main" :style="{marginLeft}">
    <op-header :height="headHeight">
      <op-nav></op-nav>
      <!--<update-password></update-password>-->
      <!--<my-task></my-task>-->
      <!--<date></date>-->
    </op-header>
    <div class="op-content" :style="{top:headHeight}">
      <div class="op-content_inner">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
  import opNav from './nav.vue'
  import date from './date.vue'
  import myTask from './myTask.vue'
  import updatePassword from './updatePassword.vue'
  import opHeader from './header.vue'
  export default {
    name: 'op-body',
    // 需要从外部传递进来
    props: {
      headHeight: {
        type: String,
        default: '60px'
      },
      marginLeft: {
        type: String,
        default: '200px'
      }
    },
    components: {opNav, date, myTask, updatePassword, opHeader}
  }
</script>

<style lang="less">
  .op-main {
    height: 100%;
    position: relative;
    overflow: auto;
  }

  .op-content {
    position: absolute;
    width: 100%;
    bottom: 0;

    .op-content_inner{
      height:100%;
      padding:0 10px;
    }
  }
</style>



